#contact  {
  background-image: url('../img/tile_3.png');
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  font-family: 'Comfortaa', cursive;
  color: white;
}

#contact-header {
  margin-top: 5vh;
  height: 25vh;
}

@media only screen and (max-width: 1000px) {
  #contact-header {
    height: 20vh;
  }
}

#contact-form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 75%;
}

form {
  text-align: center;
  width: 100%;
}

input {
  font-family: 'Comfortaa', cursive;
  background-color: #222;
  color: white;
  margin-top: 20px;
  padding: .5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  line-height: 1.5;
  border-radius: 5px;;
  border: 1.5px solid #ccc;
}

input:focus {
  background-color: #222;
  color: white;
  border-color: #007eff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

textarea {
  font-family: 'Comfortaa', cursive;
  background-color: #222;
  color: white;
  margin-top: 20px;
  padding: .5rem;
  font-size: 16px;
  width: 100%;
  display: block;
  line-height: 1.5;
  border-radius: 5px;;
  border: 1.5px solid #ccc;
}

textarea:focus {
  background-color: #222;
  color: white;
  border-color: #007eff;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(0, 126, 255, 0.1);
  outline: none;
}

.error {
  background-color: #222;
  color: white;
  border-color: #EF233C;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(255, 0, 0, 0.1);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075),
    0 0 0 3px rgba(255, 0, 0, 0.1);
}

.input-feedback {
  margin-top: 5px;
  color: #EF233C;
}

button {
  max-width: 150px;
  margin: 20px 0;
  padding: 12px 20px;
  border-style: none;
  border-radius: 5px;
  background-color: #08c;
  -webkit-box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
          box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  font-size: 17px;
  font-weight: 500;
  color: #fff;
  cursor: pointer;
  outline: none;
  -webkit-appearance: none;
}

button + button {
  margin-left: 0.5rem;
}

button:disabled {
  opacity: .8;
  cursor: not-allowed !important;
}

button.outline {
  background-color: #eee;
  border: 1px solid #aaa;
  color: #555;
}



